.wadi_content_toggle_switch_label {
  position: relative;
  display: inline-block;
  width: 4.5em;
  height: 2.3em;
  vertical-align: middle;
}
.wadi_content_toggle_switch_label .wadi_content_toggle_switch {
  display: none;
}

.wadi_content_toggle_switcher_button {
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.wadi_content_toggle_heading_1,
.wadi_content_toggle_heading_2 {
  cursor: pointer;
}

.wadi_content_toggle_switch_slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.wadi_content_toggle_switch_slider:before {
  position: absolute;
  content: "";
  height: 1.8em;
  width: 1.8em;
  left: 0.25em;
  bottom: 0.25em;
  background-color: #f1f1f1;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.wadi_content_toggle_switch + .wadi_content_toggle_switch_slider {
  background-color: #7a7a7a;
}
.wadi_content_toggle_switch + .wadi_content_toggle_switch_slider.wadi_content_toggle_switch_round {
  border-radius: 50px;
}

.wadi_content_toggle_switch:checked + .wadi_content_toggle_switch_slider {
  background-color: #d2d2d2;
}

.wadi_content_toggle_switch:checked + .wadi_content_toggle_switch_slider::before {
  -webkit-transform: translateX(2.1em);
  -ms-transform: translateX(2.1em);
  transform: translateX(2.1em);
}

.wadi_content_toggle_switch_slider.wadi_content_toggle_switch_round::before {
  -webkit-border-radius: 50%;
  border-radius: 50%;
}

.elementor-widget-wadi-content-toggle-addon .wadi_content_toggle_heading_1,
.elementor-widget-wadi-content-toggle-addon .wadi_content_toggle_heading_2 {
  margin: 0;
  padding: 0;
}

.wadi_content_toggle_switcher {
  -js-display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  padding: 25px;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  -moz-box-orient: horizontal;
  -moz-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
}

.wadi_content_toggle_sections {
  padding: 25px;
}